export const ADD_LAYER = "ADD_LAYER"
export const REMOVE_LAYER = "REMOVE_LAYER"
export const UPDATE_LAYER = "UPDATE_LAYER"
export const SEND_MAP = "SEND_MAP"
export const SEND_DRAW = "SEND_DRAW"
export const REFRESH_LAYER_RENDER = "REFRESH_LAYER_RENDER"
export const REMOVE_PROPS_FROM_LAYER = "REMOVE_PROPS_FROM_LAYER"
export const ADD_EMPTY_PROP_TO_LAYER = "ADD_EMPTY_PROP_TO_LAYER"
export const UPDATE_LAYER_PROPS = "UPDATE_LAYER_PROPS"
export const CLEAR_LAYERS = "CLEAR_LAYERS"
export const SET_DRAW_CONTROL = "SET_DRAW_CONTROL"
export const REMOVE_DRAW_CONTROL = "REMOVE_DRAW_CONTROL"
export const START_LAYER_EDIT = "START_LAYER_EDIT"
export const FINISH_LAYER_EDIT = "FINISH_LAYER_EDIT"
export const RENAME_LAYER = "RENAME_LAYER"


export const addLayer = (data, name, id, dataType, local) => dispatch => {
  dispatch({
    type: ADD_LAYER,
    data,
    name,
    id,
    dataType,
    local
  })
}

export const removeLayer = (name, seed) => dispatch => {
  dispatch({
    type: REMOVE_LAYER,
    name,
    seed
  })
}

export const clearLayers = () => dispatch => {
  dispatch({
    type: CLEAR_LAYERS
  })
}

export const sendMapObject = (map) => dispatch => {
  dispatch({
    type: SEND_MAP,
    map
  })
}

export const updateLayer = (layerData) => dispatch => {
  dispatch({
    type: UPDATE_LAYER,
    layerData
  })
}


export const refreshLayerRender = () => dispatch => {
  dispatch({
    type: REFRESH_LAYER_RENDER
  })
}


export const removePropsFromLayer = (props, layerId) => dispatch => {
  dispatch({
    type: REMOVE_PROPS_FROM_LAYER,
    props,
    layerId
  })
}

export const addEmptyPropFieldToLayer = (layerId) => dispatch => {
  dispatch({
    type: ADD_EMPTY_PROP_TO_LAYER,
    layerId
  })
}

export const updateLayerProps = (layerId, propsChangeData) => dispatch => {
  dispatch({
    type: UPDATE_LAYER_PROPS,
    layerId,
    propsChangeData
  })
}

export const setDrawControl = (drawType) => dispatch => {
  dispatch({
    type: SET_DRAW_CONTROL,
    drawType
  })
}

export const removeDrawControl = () => dispatch => {
  dispatch({
    type: REMOVE_DRAW_CONTROL
  })
}

export const startLayerEdit = (layerId) => dispatch => {
  dispatch({
    type: START_LAYER_EDIT,
    layerId
  })
}

export const finishLayerEdit = (layerId) => dispatch => {
  dispatch({
    type: FINISH_LAYER_EDIT,
    layerId
  })
}

export const renameLayer = (prevName, newLayer) => dispatch => {
  dispatch({
    type: RENAME_LAYER,
    prevName,
    newLayer
  })
}
