export const TOGGLE_DRAWER_STATE = "TOGGLE_DRAWER_STATE"
export const SET_DRAWER_STATE = "SET_DRAWER_STATE"
export const SET_DRAWER_TAB = "SET_DRAWER_TAB"

export const SET_DRAWER_OPTION = "SET_DRAWER_OPTION"

export const toggleDrawerState = () => dispatch => {
  dispatch({
    type: TOGGLE_DRAWER_STATE
  })
}


export const setDrawerState = (drawerState) => dispatch => {
  dispatch({
    type: SET_DRAWER_STATE,
    drawerState
  })
}


export const setDrawerOption = (id) => dispatch => {
  dispatch({
    type: SET_DRAWER_OPTION,
    id
  })
}
