import React from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import mapboxgl from 'mapbox-gl'

import * as Images from '../../../res/'

import Layer from '../layer'
import Instrument from '../instrument'
import {setDrawerState, toggleDrawerState, setDrawerOption} from '../../../redux/actions/drawer.js'
import {addLayer, removeLayer} from '../../../redux/actions/map'

var equal = require('fast-deep-equal/react');

const toProps = state =>({
  drawerState: state.commonReducer.drawerState,
  drawerOption: state.commonReducer.drawerOption,
  layers: state.commonReducer.layers,
  map: state.commonReducer.map
})

class Drawer extends React.Component {

  constructor(props){
    super(props)

    this.state = {
      contentData: []
    }
    this.marker = undefined

  }


  loadLayers(){
    let baseUrl = "https://gisanalytics.kz:8080/geoserver"
    let typeStorage = []
    fetch(baseUrl+"/rest/layers.json",{headers:{
      "pepelaugh":"admin"
    }})
    .then(res=>res.json())
    .then(res=>res)
    .then(res=>{
      res.layers.layer.forEach(
        (item, i)=>{
          fetch(item.href)
          .then(res=>res.json())
          .then(res=>{typeStorage[i]=res.layer.defaultStyle.name})
          .then(
            fetch(`${baseUrl}/wfs?service=wfs&version=2.0.0&request=GetFeature&typeName=${item.name}&outputFormat=application/json&srsName=EPSG:4326`).then(res=>res.json()).then(res=>{
              this.props.addLayer(res, item.name, this.props.layers.length, typeStorage[i])
            })
          )
        }
      )
    })
  }

  componentDidUpdate(prevProps, prevState, snapshot){
      if(this.props.drawerOption!==prevProps.drawerOption){
        switch (this.props.drawerOption) {
          default:
            this.setState({...prevState, contentName: "", contentData: [] })
            break;
          case 1:
            // this.loadLayers()
            this.setState({...this.state, contentName: "", contentData: this.props.layers.map((item,i)=>(<Layer id={item.id} seed={item.seed} local={item.local} name={item.name} type={item.dataType} data={item.data} key={i}/>))})
            break;
          case 2:
            let data = (
              <div className="contentContainer">
                <Instrument icon={Images['map_location_svg']} name={" "} desc={"    "} actionType="buffer"/>
                <Instrument icon={Images['map_location_svg']} name={" "} desc={"    "} actionType="simplify"/>
                <Instrument icon={Images['network_svg']} name={" "} desc={"   "}/>
                <Instrument icon={Images['route_svg']} name={" "} desc={"    "}/>
                <Instrument icon={Images['layers_1_svg']} name={" "} desc={"    "}/>
              </div>
            )
            this.setState({...prevState, contentName:  "", contentData: data});
            break;
          case 3:
            this.setState({...prevState, contentName: "", contentData: []})
            break;
        }
      }

      if(!equal(this.props.layers, prevProps.layers)){
          this.props.setDrawerOption(-1)
          setTimeout(()=>{this.props.setDrawerOption(1)}, 20)
      }
  }

  render () {
    let drawerHeaderButtons = [{
        icon: Images['share_1_svg'],
        name: "share",
        action: () => {}
      },
      {icon: Images['map_location_svg'],
      name: "here",
        action: ()=>{
          if("geolocation" in navigator){
            navigator.geolocation.getCurrentPosition((t)=>{
              if(this.marker)
                this.marker.remove()
              this.marker = new mapboxgl.Marker().setLngLat([t.coords.longitude, t.coords.latitude]).addTo(this.props.map)
              this.props.map.jumpTo({center:[t.coords.longitude, t.coords.latitude], zoom: 14})
            })
          }

        }
      },
      {
        icon: Images['props_svg'],
        name: "props",
        action: () => {}
      },
      {
        icon: Images['back_svg'],
        name: "back",
        action: () => {
          this.props.setDrawerState(false)
        }
      },
      {
        icon: Images['more_1_svg'],
        name: "more",
        action: () => {}
      }
    ]

    let fileRef = React.createRef()

    return(
      <>
        <div className={`drawer ${this.props.drawerState?"drawerVisible":"drawerHidden"}`}>
          <div className="drawerHeader">
            <div className="drawerHeaderButtonContainer">
              {
                drawerHeaderButtons.map(
                  (item, i)=>
                    <div className="drawerHeaderButton" onClick={item.action} key={i}>
                      <img src={item.icon} alt={item.icon.toString()}/>
                    </div>
                )
              }
            </div>
          </div>
          <div className="drawerContent">
            <div className="drawerContentHeader">
              <p className="contentName">{this.state.contentName}</p>
              <div className="newLayerButton">
                <img src={Images['add_1_svg']} alt="New Layer" onClick={
                    ()=>{
                      this.setState({...this.state,  creationType: true, layerTypeMenu: false})
                    }
                  }/>
                <div className={`layerMenusCloseButton ${this.state.layerTypeMenu || this.state.creationType?"menuVisible":"menuHidden"}`} onClick={()=>{this.setState({...this.state, createNew: false, creationType: false, layerTypeMenu: false})}}>x</div>
                <div className={`creationTypeMenu ${this.state.creationType?"menuVisible":"menuHidden"}`}>
                  <p onClick={()=>{
                      this.setState({...this.state, createNew: true, creationType: false, layerTypeMenu: true});
                    }}>New</p>
                  <p onClick={()=>{
                      this.setState({...this.state, createNew: false, creationType: false, layerTypeMenu: true})}
                  }>Upload...</p>
                </div>
                <div className={`layerTypeMenu ${this.state.layerTypeMenu?"menuVisible":"menuHidden"}`}>
                  <p onClick={()=>{
                      if(this.state.createNew){
                        this.setState({...this.state, creationType: false, layerTypeMenu: false})
                        this.props.addLayer({features: [], type: "FeatureCollection"}, "New Layer", this.props.layers.length, "polygon", true);
                      }else{
                        this.setState({...this.state, creationType: false, layerTypeMenu: false,type: "polygon"})
                        fileRef.current.click()
                      }
                    }}>Polygon</p>
                  <p onClick={()=>{
                    if(this.state.createNew){
                      this.setState({...this.state, creationType: false, layerTypeMenu: false})
                      this.props.addLayer({features: [], type: "FeatureCollection"}, "New Layer", this.props.layers.length, "point", true);
                    }else{
                      this.setState({...this.state, creationType: false, layerTypeMenu: false, type: 'point'})
                      fileRef.current.click()
                    }
                    }}>Point</p>
                  <p onClick={()=>{
                    if(this.state.createNew){
                      this.setState({...this.state, creationType: false, layerTypeMenu: false})
                      this.props.addLayer({features: [], type: "FeatureCollection"}, "New Layer", this.props.layers.length, "line", true);
                    }else{
                      this.setState({...this.state, creationType: false, layerTypeMenu: false,  type: 'line'})
                      fileRef.current.click()
                    }
                    }}>Polyline</p>
                </div>
              </div>
            </div>
            <input className="newLayerFileInput" ref={fileRef} type="file" style={{visibility:"collapse"}} onChange={(e)=>{
                e.persist();

                let reader = new FileReader()

                reader.readAsText(e.target.files[0])

                reader.onloadend = (ev)=>{

                  this.props.addLayer(JSON.parse(reader.result), e.target.files[0].name, this.props.layers.length, this.state.type, true);
                }
              }}/>
            <div className="drawerContentContent">
              {
                this.state.contentData
              }
            </div>

          </div>
        </div>
      </>
    )
  }
}

export default connect(toProps, {setDrawerState, toggleDrawerState, setDrawerOption, addLayer, removeLayer})(Drawer);
