import React from 'react'
import mapboxgl from 'mapbox-gl'

import { connect } from 'react-redux'

import Layer from '../layer'

import store from '../../../redux/store.js'

// import {sendEditorTitle, sendEditorContent, toggleEditorState} from '../../../redux/actions/editor'
import {addLayer} from '../../../redux/actions/map'
import * as turf from '@turf/turf'
import {setCabinetState, setCabinetTitle, setCabinetContent} from '../../../redux/actions/cabinet'

const toProps = state => ({
  map: state.commonReducer.map,
  editorState: state.commonReducer.editorState,
  layers: state.commonReducer.layers
})


class Instrument extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      icon: this.props.icon,
      name: this.props.name,
      desc: this.props.desc,
      currentValues: [0.1,36,'kilometers']
    }
  }

  openLayerEditor(title, content){
    this.state.currentElement = null
    this.props.setCabinetTitle(title)
    this.props.setCabinetContent(content)
    this.props.setCabinetState(true)
  }

  render () {

    return(<>
      <div className="instrument" onClick={()=>{
        let content = []
        let params = []
        let defaultValues = []
            switch(this.props.actionType){
              default:
                return
              case "simplify":
                params = ['layer', 'tolerance', 'highQuality']
                defaultValues = [undefined, 0.0004, true]

                params.forEach((item, i)=>{
                  if(item==="layer"){

                    let options = []

                    this.props.layers.forEach((item, i)=>{
                      if(!this.state.currentElement)
                        this.state.currentElement = item
                      options.push(
                        <option
                          value={item.id}
                          onClick={(e)=>{
                            this.setState({...this.state, currentElement: item})
                          }}>{item.name}</option>
                      )
                    })

                    content.push(
                      <>
                        <div className="editorItem">
                          <label htmlFor={item}>{item+":"}</label>
                          <select name={item}>
                            {options}
                          </select>
                        </div>
                      </>
                    )
                  } else
                    content.push(
                      <>
                        <div className="editorItem">
                          <label htmlFor={item}>{item+":"}</label>
                          <input type="text" name={item} placeholder={defaultValues[i].toString()} onChange={(e)=>{
                              e.persist()
                              let l = this.state.currentValues
                              l[i-1] = e.target.value
                              this.setState({...this.state, currentValues:l})
                            }}/>
                        </div>
                      </>
                    )
                })

                content.push(
                  <button name="runSimplify" onClick={()=>{
                      if(this.state.currentElement===undefined){
                        // TODO toast with error message
                        return;
                      }

                        let data = []
                        let v = Object.assign({}, this.state.currentValues)

                        data = turf.simplify(this.state.currentElement.data, {tolerance: v[0], highQuality: v[1]})
                        let sourceName = "simplified_"+this.state.currentElement.id+"_"+(Math.random()*12500)
                        let center = turf.centroid(data).geometry.coordinates
                        let area = turf.area(data)
                        this.props.addLayer(data, "Simplification of "+this.state.currentElement.name, this.props.layers.length, "polygon", true)
                    }}> </button>
                )
                this.openLayerEditor(" ", content);
                break;
              case "buffer":
                content = []

                params = ["layer", "radius", "steps", "units"]
                defaultValues = [undefined, 0.1, 36, "kilometers"]

                params.forEach((item, i)=>{
                  if(item==="layer"){

                    let options = []

                    this.props.layers.forEach((item, i)=>{
                      if(!this.state.currentElement)
                        this.state.currentElement = item
                      options.push(
                        <option
                          value={item.id}
                          onClick={(e)=>{
                            this.setState({...this.state, currentElement: item})
                          }}>{item.name}</option>
                      )
                    })
                    content.push(
                      <>
                        <div className="editorItem">
                          <label htmlFor={item}>{item+":"}</label>
                          <select name={item} className={this.state.currentElement===undefined?"unpopulatedSelection":""}>
                            {options}
                          </select>
                        </div>
                      </>
                    )
                  } else
                    content.push(
                      <>
                        <div className="editorItem">
                          <label htmlFor={item}>{item+":"}</label>
                          <input type="text" name={item} placeholder={defaultValues[i]} onChange={(e)=>{
                              e.persist()
                              let l = this.state.currentValues
                              l[i-1] = e.target.value
                              this.setState({...this.state, currentValues:l})
                            }}/>
                        </div>
                      </>
                    )
                })
                content.push(
                  <button name="runBuffer" onClick={()=>{
                      if(this.state.currentElement===undefined)
                        return;

                        let data = []
                        let v = this.state.currentValues

                        data = turf.buffer(this.state.currentElement.data, v[0],{steps: v[1]  , units: v[2]})

                        let sourceName = "buffer_"+this.state.currentElement.id
                        let center = turf.centroid(data).geometry.coordinates
                        let area = turf.area(data)

                        this.props.addLayer(data, "Buffer of "+this.state.currentElement.name, this.props.layers.length, "polygon", true)


                    }}>  </button>
                )
                this.openLayerEditor(" ", content)
                break;
            }
        }}>
        <img src={this.state.icon} className="instrumentIcon" alt=""/>
        <div className="instrumentTextContainer">
          <p className="instrumentName">{this.state.name}</p>
          <p className="instrumentDescription">{this.state.desc}</p>
        </div>
      </div>
    </>)
  }

}

export default connect(toProps, {addLayer, setCabinetTitle, setCabinetContent, setCabinetState})(Instrument);
