import React from 'react'
import PropTypes from 'prop-types'
import * as Images from '../../../res/'
import {connect} from 'react-redux'

import {setCabinetState, setCabinetTitle, setCabinetContent} from '../../../redux/actions/cabinet'

const toProps = state =>({
  cabinetState: state.commonReducer.cabinetState,
  cabinetTitle: state.commonReducer.cabinetTitle,
  cabinetContent: state.commonReducer.cabinetContent
})

class Cabinet extends React.Component {
  render () {
    return (
      <>
        <div className={`cabinet ${this.props.cabinetState?"cabinetVisible":"cabinetHidden"}`}>
          <div className={`cabinetTitle`}>{this.props.cabinetTitle}</div>
          <div className={`cabinetContent`}>
            {this.props.cabinetContent}
          </div>
          <div className={`cabinetCloseButton`} onClick={()=>{
              this.props.setCabinetContent([])
              this.props.setCabinetTitle("")
              this.props.setCabinetState(false)
            }}>
          <img src={Images['error_svg']} alt=""/></div>
        </div>

      </>
    )
  }




}

export default connect(toProps, {setCabinetState, setCabinetTitle, setCabinetContent})(Cabinet);
