import React from 'react'
import PropTypes from 'prop-types'
import * as Images from '../../../res/'
import {connect} from 'react-redux'
import ColorPicker from 'rc-color-picker'
import * as turf from '@turf/turf'
import 'rc-color-picker/assets/index.css';
import {removeLayer, renameLayer, removePropsFromLayer, addEmptyPropFieldToLayer, updateLayerProps, setDrawControl, removeDrawControl, startLayerEdit, finishLayerEdit} from '../../../redux/actions/map'

import {setCabinetState, setCabinetContent} from '../../../redux/actions/cabinet'


const toProps = state => ({
  layers: state.commonReducer.layers,
  map: state.commonReducer.map,
  draw: state.commonReducer.draw,
  currentlyEditedLayer: state.commonReducer.currentlyEditedLayer
})

let fillProps = [
    {
      name: "fill-antialias",
      type: "checkbox"
    }, {
      name: "fill-color",
      type: "color"
    }, {
      name: "fill-opacity",
      type: "range",
      dimensions: [
        0, 1
      ],
      step: 0.01
    }, {
      name: "fill-outline-color",
      type: "color"
    }
  ]
let lineProps = [{
    name: "line-blur",
    type: "text",
  }, {
    name: "line-color",
    type: "color"
  }, {
    name: "line-dasharray",
    type: "text"
  }, {
    name: "line-gap-width",
    type: "color"
  }]

let circleProps = [
  {
    name:"circle-blur",
    type:"range",
    dimensions: [
      0, 1
    ],
    step: 0.01
  },
  {
    name: "circle-color",
    type: "color"
  },
  {
    name: "circle-opacity",
    type:"range",
    dimensions: [
      0, 1
    ],
    step: 0.01
  },
  {
    name: "circle-radius",
    type:"range",
    dimensions: [
      0, 100
    ],
    step: 1
  },
  {
    name: "circle-stroke-color",
    type: "color"
  }, {
    name: "circle-stroke-opacity",
    type:"range",
    dimensions: [
      0, 1
    ],
    step: 0.01
  }, {
    name: "circle-stroke-width",
    type:"range",
    dimensions: [
      0, 10
    ],
    step: 0.1
  }
]


class Layer extends React.Component {

  constructor(props){
    super(props)
      this.state = {
        name: this.props.name,
        type: this.props.type,
        visibility: true,
        renamePanelRef: React.createRef()
      }

  }

  openLayerEditor(data){
    this.props.setCabinetContent([])

    if(data){
      this.props.setCabinetContent(data)
    }else{
      let temp = []
      turf.propEach(this.props.data, (propsObj, i)=>{
        if(!propsObj){
          propsObj = {}
        }
        Object.keys(propsObj).forEach((item, i)=>{
          if(!temp[item]){
            temp[item] = {type: "String"}
          }
        })
      })
      let tempRefs = []
      let newContent =
      (
        <>
        <table>
          <thead>
            <tr>
              <td></td><td>Name</td><td>Delete?</td>
            </tr>
          </thead>
          <tbody>
            {Object.keys(temp).map((item, i)=>{
              tempRefs[i] = {name: item,  cbRef: React.createRef(), textRef: React.createRef()}
              return (<tr>
                <td>{i}</td>
                <td>
                  <input type="text" ref={tempRefs[i].textRef} defaultValue={item.toString()}/>
                </td>
                <td><input ref={tempRefs[i].cbRef} type="checkbox"></input></td>
              </tr>)
              }
            )}
          </tbody>
        </table>
        <button onClick={()=>{
          this.props.setCabinetState(false)
          this.props.addEmptyPropFieldToLayer(this.props.id);
          this.openLayerEditor()
          }
        }>Add new field</button>
        <button onClick={
          ()=>{
              let newLayerProps = {}
              tempRefs.forEach(
               item=>{
                   newLayerProps[item.name]= item.textRef.current.value
               }
             )
              this.props.updateLayerProps(this.props.id, newLayerProps)
          }
        }>Save changes</button>
        <button onClick={()=>{
              this.props.setCabinetState(false)
              this.props.removePropsFromLayer(tempRefs.filter((item)=>item.cbRef.current.checked===true).map((item)=>item.name), this.props.id);
              this.openLayerEditor()
            }
          }>Delete selected </button>
        </>
      )

      this.props.setCabinetContent(newContent)
    }
    this.props.setCabinetState(true)
  }

  toggleLayerVisibility(){
    if(this.props.currentlyEditedLayer===this.state.name+"_layer_"+this.props.seed)
    return
    var visibility = this.props.map.getLayoutProperty(this.state.name+"_layer_"+this.props.seed, 'visibility');

    if (visibility === 'visible' || visibility === undefined) {
      this.props.map.setLayoutProperty(this.state.name+"_layer_"+this.props.seed, 'visibility', 'none');
      this.setState({...this.state, visibility: false})
    } else {
      this.props.map.setLayoutProperty(this.state.name+"_layer_"+this.props.seed, 'visibility', 'visible');
      this.setState({...this.state, visibility: true})
    }
  }

  setLayerVisibility(value){
    this.props.map.setLayoutProperty(this.state.name+"_layer_"+this.props.seed, 'visibility', value?'visible':'none');
  }

  goToLayer(){
    if(this.state.center)
      {
        this.props.map.jumpTo({center: this.state.center, zoom: 13})
      }else{
        this.props.map.jumpTo({center: turf.center(this.props.layers[this.props.id].data).geometry.coordinates, zoom: 13});
      }
  }

  setPaintValue(propName, value){
    this.props.map.setPaintProperty(this.state.name+"_layer_"+this.props.seed, propName, value)
  }

  editLayerFeatures(){
    if(this.props.currentlyEditedLayer===this.state.name+"_layer_"+this.props.seed){
      this.props.finishLayerEdit(this.state.name+"_layer_"+this.props.seed)
      this.props.removeDrawControl()
      this.setLayerVisibility(true)
    }else{
      this.props.removeDrawControl()
      this.props.setDrawControl(this.state.type)
      this.props.startLayerEdit(this.state.name+"_layer_"+this.props.seed)
      this.setLayerVisibility(false)
    }
  }

  render () {
    let typeImg;
    switch(this.state.type){
      default:
      case 0:
      case "generic":
      case "polygon":
        typeImg = Images["polygon_svg"]
        break;
      case "line":
      case 1:
        typeImg = Images["polyline_svg"]
        break;
      case "point":
      case 2:
        typeImg = Images["marker_svg"]
        break;
    }

    return (

      <>
        <div className="layerItem">
          <div className="layerType">
            <img src={typeImg} alt="layer type"></img>
          </div>
          <p className="layerName">
            {this.state.name+`${this.props.local?":local":""}`}
          </p>
          <img className="layerEditButton" alt="edit layer" src={this.props.currentlyEditedLayer!==this.state.name+"_layer_"+this.props.seed?Images["edit_svg"]:Images["save_svg"]} onClick={()=>{
              this.editLayerFeatures()
            }}/>
          <img className="layerVisibilityButton" alt="toggle layer visibility" src={this.state.visibility?Images["view_svg"]:!this.state.visibility?Images["view_1_svg"]:Images["warning_svg"]} onClick={()=>{
              this.toggleLayerVisibility()
            }}/>

          <div className="goToLayerButton" onClick={()=>{
              this.goToLayer()
            }}>
            <img src = {Images['fast_forward_svg']} alt=""/>
          </div>
          <div className="layerParamsButton">
            <div className={`layerRenamePanel ${this.state.renamePanel?"":"menuHidden"} `}>
              <label htmlFor="layerRenameInput">: </label>
              <input className="layerRenameInput" key={this.state.name} type="text" ref={this.state.renamePanelRef}/>
              <button onClick={()=>{
                  if(this.props.currentlyEditedLayer===this.state.name+"_layer_"+this.props.seed){
                    this.props.finishLayerEdit(this.state.name+"_layer_"+this.props.seed)
                    this.props.removeDrawControl()
                    this.setLayerVisibility(true)
                  }
                  this.props.renameLayer(this.state.name, {seed: this.props.seed, id: this.props.id, data: this.props.data, name: this.state.renamePanelRef.current.value, dataType: this.props.type, local: this.props.local})
                  this.setState({...this.state, renamePanel: false})
                }}></button>
            </div>
            <div className={`layerMenuClose ${this.state.menuVisibility||this.state.renamePanel?"":"layerMenuHidden"}`} onClick={()=>{
              this.setState({...this.state, menuVisibility: false, renamePanel: false})
            }}><br/>X</div>
            <div className={`layerAdditionalMenu ${this.state.menuVisibility?"layerMenuVisible":"layerMenuHidden"}`}>
              <p onClick={()=>{
                  this.setState({...this.state, renamePanel: true, menuVisibility: false})
                }}></p>
              <p onClick={()=>{
                  this.setState({...this.state, menuVisibility: false})
                  if(this.props.local){
                    if(this.props.currentlyEditedLayer===this.state.name+"_layer_"+this.props.seed){
                      this.props.finishLayerEdit(this.state.name+"_layer_"+this.props.seed)
                      this.props.removeDrawControl()
                      this.setLayerVisibility(true)
                    }
                    this.props.removeLayer(this.state.name,this.props.seed)
                  }else{
                    let baseUrl = "https://gisanalytics.kz:8080/geoserver/rest"

                    fetch(`${baseUrl}/layers/${this.state.name}`, {
                      headers: {
                        "pepelaugh": ""
                      },
                      method: 'DELETE'
                    }).then(res => res.text()).then(res => {
                      console.log(res)
                    })
                  }
              }}> </p>
              <p onClick={()=>{
                  require('file-saver').saveAs(new Blob([JSON.stringify(this.props.data)], {type: "application/geo+json;charset=utf-8"}), `${this.state.name}.geojson`)
              }}>...</p>
              <p onClick={()=>{
                this.setState({...this.state, menuVisibility: false})
                this.openLayerEditor()
                }}>  </p>
              <p onClick={()=>{
                this.setState({...this.state, menuVisibility: false})
                  let data
                  let targ
                  switch(this.props.type){
                    case "polygon":
                    case "generic":
                    default:
                      targ = fillProps
                      break;
                    case "line":
                    case 1:
                      targ = circleProps
                      break;
                    case "point":
                    case 2:
                      targ = lineProps
                  }

                  data = Array.prototype.map.call(targ, (item, i)=>{
                    let it

                    switch(item.type){
                      default:
                      case "checkbox":
                        it = (
                          <input className={`field_${item.name}`} type="checkbox" onChange={(e)=>{
                              this.setPaintValue(item.name, e.target.checked)
                          }}/>
                        )
                        break;
                      case "range":
                        it = (
                          <input className={`field_${item.name}`} type="range" min={item.dimensions[0]} max={item.dimensions[1]} step={item.step} onChange={(e)=>{
                              this.setPaintValue(item.name, Number(e.target.value))
                            }}/>
                        )
                        break;
                      case "text":
                        it = (
                          <input className={`field_${item.name}`} type="text" onChange={
                              (e)=>{
                                this.setPaintValue(item.name, e.target.value)
                              }
                            }/>
                        )
                        break;
                      case "color":
                        it = (
                          <ColorPicker
                            color={'#00F'} alpha={100} onChange={
                              (e)=>{
                                this.setPaintValue(item.name, e.color)
                              }
                            } placement={'bottomLeft'} className={`field_${item.name}`}/>
                        )
                    }
                    return (
                      <div className="editorItem" key={Math.random()}>
                        <label htmlFor={`field_${item.name}`}>{item.name}</label>
                        {
                          it
                        }
                      </div>
                      )
                    })
                this.openLayerEditor(data)
              }}>  </p>
            </div>
            <img src={Images["more_1_svg"]} alt="" onClick={()=>{
                this.setState({...this.state, menuVisibility: true, renamePanel: false})
              }}/>
          </div>
        </div>
      </>
    )
  }
}

export default connect(toProps, {removeLayer,setCabinetState, setCabinetContent, removePropsFromLayer, addEmptyPropFieldToLayer, updateLayerProps,renameLayer, setDrawControl, removeDrawControl, startLayerEdit, finishLayerEdit})(Layer);
