import React from 'react'

import {connect} from 'react-redux'

import {setDrawerState, toggleDrawerState, setDrawerOption} from '../../../redux/actions/drawer'
import {setCabinetState, toggleCabinetState} from '../../../redux/actions/cabinet'


import * as Images from '../../../res/'

const toProps = state => ({
  drawerOption: state.commonReducer.drawerOption,
  drawerState: state.commonReducer.drawerState
})

class Sidebar extends React.Component {
  render () {
    return (
      <>
        <div className="sidebar">
          {this.props.data.buttons.map(
            (item, i)=>
              <div className="sidebarButton" key={i} onClick={()=>{
                  switch(item.type){
                    default:
                    case "drawer":
                      this.props.setCabinetState(false)
                      if(i!==this.props.drawerOption){
                        this.props.setDrawerState(true)
                        this.props.setDrawerOption(i)
                      }else{
                        this.props.setDrawerState(!this.props.drawerState)
                      }
                      break;
                    case "cabinet": {
                      this.props.toggleCabinetState()
                      this.props.setDrawerState(false)
                    }
                  }
                }}>
                <img src={Images[item.icon]} alt=""/>
              </div>
          )}
        </div>
      </>
    )
  }
}

export default connect(toProps,{setDrawerState, toggleDrawerState, setDrawerOption, setCabinetState, toggleCabinetState})(Sidebar);
