import React from 'react'
import mapboxgl from 'mapbox-gl'

import MapboxGeocoder from 'mapbox-gl-geocoder'
import '@mapbox/mapbox-gl-draw/dist/mapbox-gl-draw.css'
import 'mapbox-gl-geocoder/dist/mapbox-gl-geocoder.css'
import {connect} from 'react-redux'
import {sendMapObject, addLayer, clearLayers, refreshLayerRender} from '../../redux/actions/map'
let styles = [
  {
    url: 'mapbox://styles/youthanasia/ckff7scja0zxa1api15mhysci',
    name: ""
  }, {
    url: 'mapbox://styles/youthanasia/ckfp7doca0t5k19pp86phi889',
    name: ""
  }
]

const toProps = state => ({
  map: state.commonReducer.map,
  layers: state.commonReducer.layers
})


class Map extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      lng: 71.43,
      lat: 51.15,
      zoom: 10
    }

    this.drawControl = null
  }

  componentDidMount() {
    const map = new mapboxgl.Map({
      container: this.mapContainer,
      style: 'mapbox://styles/youthanasia/ckff7scja0zxa1api15mhysci',
      center: [
        this.state.lng, this.state.lat
      ],
      zoom: this.state.zoom
    });

    map.on('load',()=>{
      map.addControl(new MapboxGeocoder({
        accessToken: mapboxgl.accessToken,
        mapboxgl: mapboxgl
      }))
      map.addControl(new mapboxgl.NavigationControl());
      map.addControl(new mapboxgl.GeolocateControl());
    })
    this.props.sendMapObject(map)
  }

  onGeocoderSelect(data){
    this.setState({geocoderData: data})
  }


  addLayersToMap(){
    let temp = this.props.layers;
    this.props.clearLayers()
    this.props.map.on('style.load',()=>{
      temp.forEach((item, i)=>{
        this.props.addLayer(item.data, item.name, item.id, item.dataType, item.local)
      })
    })
  }


  render(){
    return(
      <>
        <div className='mapContainer' ref = {el => this.mapContainer = el}/>
        <div className="styleSelectContainer">
          {
            styles.map((item, i)=>
              <button className="styleSelector" name={item.name} key={i} onClick={(e)=>{
                  this.props.sendMapObject(this.props.map.setStyle(this.props.map.getStyle()).setStyle(item.url, {diff: false}))
                  this.addLayersToMap()
                }}>{item.name}</button>
            )
          }
        </div>
      </>
    )
  }
}

export default connect(toProps, {sendMapObject, addLayer, clearLayers, refreshLayerRender})(Map);
