import React from 'react'

import {connect} from 'react-redux'
import {removeLayer, addLayer, refreshLayerRender} from '../../../redux/actions/map'
import * as turf from '@turf/turf'

const toProps = state => ({
  layers: state.commonReducer.layers,
  map: state.commonReducer.map,
  draw: state.commonReducer.draw
})


class Popup extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      fields: props.fields,
      target: turf.truncate(this.props.target, {precision: 8, coordinates: 2}),
      defaultValues: {},
      refs: {},
      data: {}
    }

    props.fields.forEach((item)=>{
      this.state.defaultValues[item.label] = item.value;
      this.state.refs[item.label] = React.createRef()
    })

    this.state.data = props.fields.map(
              (item, i) =>{
                return (
                  <div className="popupFeatureContainer" key={i}>
                    <label htmlFor={"popupFeatureAttribute"+i}>{item.label}</label>
                    <input type="text" ref={this.state.refs[item.label]} id={"popupFeatureAttribute"+i} defaultValue={item.value}/>
                  </div>
                )
              }
            )
  }



  render () {
    return (
      <>
      <div className="tableContainer">
        <div className="tableHeader">
          <h4>Property</h4> <h4>Data</h4>
        </div>
        {
          this.state.data
        }
      </div>
      <button onClick = {
        () => {
          let layer = this.props.layers.filter((item, i)=>{
            return item.name+"_layer_"+item.seed === this.props.target.layer.id
          })[0] // find layer object in reducer.layers array which contains target feature
          if (layer) {
            let feature = layer.data.features.filter((item, i) => turf.booleanEqual(item.geometry, this.state.target.geometry))[0] // find target feature in reducer.layer.data filtering by coordinate equality
            console.log(layer.data.features);
            console.log(this.state.target.geometry);
            if (feature) {
              let newValues = this.state.defaultValues;
              this.props.fields.forEach((item, i) => {
                newValues[item.label] = this.state.refs[item.label].current.value
              })

              feature.properties = newValues
              let restOf = layer.data.features.filter((item, i)=>!turf.booleanEqual(item.geometry,this.state.target.geometry))
              layer.data.features = [feature, ...restOf]
              this.props.removeLayer(layer.name, layer.seed);
              this.props.addLayer(layer.data, layer.name, layer.id, layer.dataType, layer.local)
              this.props.refreshLayerRender()
            }
          }
        }
      }> Save </button>
      </>
    )
  }
}

export default connect(toProps, {removeLayer, addLayer, refreshLayerRender})(Popup);
