export const SET_CABINET_STATE = "SET_CABINET_STATE"
export const TOGGLE_CABINET_STATE = "TOGGLE_CABINET_STATE"
export const SET_CABINET_CONTENT = "SET_CABINET_CONTENT"
export const SET_CABINET_TITLE = "SET_CABINET_TITLE"


export const setCabinetState = (cabinetState) => dispatch => {
  dispatch({
    type: SET_CABINET_STATE,
    cabinetState
  })
}

export const toggleCabinetState = () => dispatch => {
  dispatch({
    type: TOGGLE_CABINET_STATE
  })
}

export const setCabinetContent = (cabinetContent) => dispatch => {
  dispatch({
    type: SET_CABINET_CONTENT,
    cabinetContent
  })
}

export const setCabinetTitle = (cabinetTitle) => dispatch => {
  dispatch({
    type: SET_CABINET_TITLE,
    cabinetTitle
  })
}
